* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 100;
  color: #fff;
}

.container {
  display: flex;
  flex-wrap: wrap;
}

.block {
  height: 300px;
  flex-basis: 300px;
  flex-shrink: 0;
  flex-grow: 1;
  text-align: center;
  line-height: 300px;
}

.block--1 {
  background-color: #f00;
}

.block--2 {
  background-color: rgb(80%, 0%, 0%);
}

.block--3 {
  background-color: rgb(60%, 0%, 0%);
}

.block--4 {
  background-color: rgb(40%, 0%, 0%);
}

.block--5 {
  background-color: rgb(20%, 0%, 0%);
}

.block--6 {
  background-color: #000;
}

@media (min-width: 600px) {
  .block {
    flex-basis: 50%;
  }
  .block--3 {
    order: 1;
  }
  .block--5 {
    order: 2;
  }
  .block--6 {
    order: 3;
  }
}

@media (min-width: 900px) {
  .block {
    flex-basis: 33.33%;
  }
  .block--4 {
    order: 3;
  }
  .block--5 {
    order: 2;
  }
  .block--6 {
    order: 1;
  }
}
